
$blue-1: #141B2C;
$blue-2: #27334F;
$blue-3: #212944;
$blue-4: #202A43;
$blue-5: #425277;

$blue-light-1: #4788C7;
$blue-light-2: #74ACE2;
$blue-light-3: #A9C8E8;
$blue-light-4: #CBDDF0;

$green-1:#82C94E;
$green-2:#B6D79E;

$red-1: #CD5050;
$red-2: #CE5151;

.btn {
    font-size: 14px;
    text-transform: uppercase;
    background-color: transparent;
    border: 0 none;
    border-radius: 10px;
    padding: 0 30px;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 40px;
    transition: all 0.15s linear;

    &-light-blue {
        background-color: $blue-light-1;
        color: $blue-light-4;

        &:focus,
        &:hover {
            background-color: $blue-light-2;
        }
    }
    &-green {
        background-color: $green-1;
        color: #fff;

        &:focus,
        &:hover {
            color: #E6E6E6;
            background-color: #98C578;
        }
    }
    &[disabled],
    &:disabled {
        color: #425277;
        background-color: $blue-3;
        cursor: default;
    }
}