
$blue-1: #141B2C;
$blue-2: #27334F;
$blue-3: #212944;
$blue-4: #202A43;
$blue-5: #425277;

$blue-light-1: #4788C7;
$blue-light-2: #74ACE2;
$blue-light-3: #A9C8E8;
$blue-light-4: #CBDDF0;

$green-1:#82C94E;
$green-2:#B6D79E;

$red-1: #CD5050;
$red-2: #CE5151;

.switch-wrapper {
    display: flex;
    align-items: center;
}
.switch-label {
    margin-right: 10px;
    font-size: 12px;

    @media (min-width: 992px) {
        margin-right: 20px;
    }
}
.switch {
    position: relative;
    display: inline-block;
    width: 60px;
    height: 32px;
    flex-shrink: 0;
    
    &-slider {
        position: absolute;
        cursor: pointer;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: $blue-3;
        transition: all 0.15s linear;
        border-radius: 34px;

        &:before {
            position: absolute;
            content: '';
            height: 26px;
            width: 26px;
            left: 4px;
            bottom: 3px;
            background-color: #425277;
            transition: all 0.15s linear;
            border-radius: 50%;
        }
    }
    &-input {
        display:none;

        &:checked {
            + .switch-slider {
                background-color: $blue-3;

                &:before {
                    transform: translateX(26px);
                    background-color: $blue-light-4;
                }
            }
        }
    }
    &:hover {
        .switch-input {
            + .switch-slider {
                background-color: $blue-2;
            }
            &:checked {
                + .switch-slider {
                    background-color: $blue-2;

                    &:before {
                        background-color: #959ca3;
                    }
                }
            }
        }
    }
}